:root {
    --main-color: #e01d3f;
}

.App-header {
    display: flex;
    align-items: center;
    color: var(--main-color);
    font-size: 20px;
    border-bottom: 3px solid var(--main-color);
}

.App-header img {
    width: 200px;
}

.App-body {
    margin: 50px;
}

.App-footer {
    border-top: 3px solid var(--main-color);
    width: 100%;
    display: flex;
    justify-content: center;
    position: fixed;
    bottom: 0;
    font-style: italic;
}
